.task-box {
  padding: 10px;
  .ant-btn {
    border-radius: 0;
  }
  .header {
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0 20px;
    border-bottom: 1px solid #eee;
  }

  .btns {
    padding: 10px;
    .ant-tag {
      padding: 2px 10px;
      border-radius: 0;
      cursor: pointer;
    }
  }
}

.ant-picker.date-pick {
  width: 100%;
}
