.vote-container {
  width: 400px;
  margin: 0 auto;
  .header {
    font-size: 20px;
    color: #333;
    border-bottom: 1px solid #eee;
    padding: 20px 10px;
    box-sizing: border-box;
    margin-bottom: 10px;
  }
  .main {
    padding: 10px;
    font-size: 16px;
  }
  .line {
    margin-top: 10px;
    &.opp {
      color: lightcoral;
    }
  }

  .footer {
    margin-top: 20px;
  }
}
